<template>
  <div>
    <div class="card" v-for="(menu, index) in menus" :key="index">
      <div class="card-body">
        <h5 class="card-title">{{ menu.fname }}</h5>
        <p class="card-text">{{ menu.fprice.toLocaleString() }} 원</p>
        <p class="card-text">{{ menu.finfo }}</p>
        <div>
          <button class="btn btn-primary" @click="toggleEditCard(index)">
            {{ showEditCardIndex === index ? "닫기" : "수정하기" }}
          </button>
          <!-- 삭제 버튼 -->
          <button class="btn btn-danger" @click="confirmDelete(menu.fnum)">
            삭제
          </button>
        </div>
      </div>
      <div v-if="showEditCardIndex === index" class="card" style="width: 18rem">
        <div class="card-body">
          <h5 class="card-title">메뉴 수정</h5>
          <div id="imagePreview">
            <img
              v-if="editedMenu.fimg"
              :src="editedMenu.fimg"
              class="rounded mx-auto d-block"
              style="max-width: 100%; max-height: 300px"
            />
          </div>
          <div id="MenuImg" class="custom-file">
            <!-- 이미지 업로드를 위한 input 요소에 ref 속성 추가 -->
            <input id="customFile" type="file" ref="image" @change="upload" />
            <label class="custom-file-label" for="customFile">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="50"
                height="50"
                fill="currentColor"
                class="bi bi-card-image"
                viewBox="0 0 16 16"
              >
                <path d="M6.002 5.5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0" />
                <path
                  d="M1.5 2A1.5 1.5 0 0 0 0 3.5v9A1.5 1.5 0 0 0 1.5 14h13a1.5 1.5 0 0 0 1.5-1.5v-9A1.5 1.5 0 0 0 14.5 2zm13 1a.5.5 0 0 1 .5.5v6l-3.775-1.947a.5.5 0 0 0-.577.093l-3.71 3.71-2.66-1.772a.5.5 0 0 0-.63.062L1.002 12v.54L1 12.5v-9a.5.5 0 0 1 .5-.5z"
                />
              </svg>
            </label>
          </div>

          <br />
          <!-- 이미지 미리보기 -->
          <div id="imagePreview">
            <img
              v-if="imageUrl"
              :src="imageUrl"
              class="rounded mx-auto d-block"
              style="max-width: 100%; max-height: 300px"
            />
          </div>
          <input
            type="text"
            class="form-control"
            v-model="editedMenu.fname"
            required
          />
          <input
            type="number"
            class="form-control"
            v-model="editedMenu.fprice"
            required
          />
          <p>메뉴 설명</p>
          <input
            type="text"
            class="form-control"
            v-model="editedMenu.finfo"
            required
          />
          <div>
            <input
              type="radio"
              id="optionYes"
              class="form-check-input"
              v-model="editedMenu.foption"
              value="1"
            />
            <label for="optionYes">옵션 추가</label>
            <input
              type="radio"
              id="optionNo"
              class="form-check-input"
              v-model="editedMenu.foption"
              value="2"
            />
            <label for="optionNo">옵션 추가 안 함</label>
          </div>
          <button class="btn btn-success" @click="saveChanges">저장</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "B_MenuPluse",
  props: ["bnum"],
  data() {
    return {
      menus: [],
      showEditCardIndex: -1,
      editedMenu: {
        fname: "",
        fprice: 0,
        finfo: "",
        foption: "1",
        fimg: this.imageUrl,
      },
      imageUrl: null,
      image: null,
    };
  },
  created() {
    this.menuList(); // 컴포넌트가 생성될 때 메뉴 목록을 가져오도록 호출
  },
  methods: {
    async menuList() {
      try {
        const res = await axios.post(`/B_MenuInfo?bnum=${this.bnum}`);
        this.menus = res.data.sort(
          (a, b) => new Date(b.fnum) - new Date(a.fnum)
        );
        console.log("Response: ", res.data);
        console.log("Menus: ", this.menus);
      } catch (error) {
        console.log("Error fetching menus:", error);
      }
    },

    // 사진 변환 메소드 (base64 형식: 데이터를 텍스트 형식으로 변환하는 인코딩 방식{데이터 크기가 증가함})
    async upload() {
      // 사용자가 선택한 이미지 파일을 가져옴
      this.image = this.$refs.image[0].files[0]; // 수정

      console.log(this.image); // 추가

      // 만약 이미지가 선택되지 않은 경우에는 처리를 종료
      if (!this.image) return;

      // 객체를 생성, 이 객체를 사용하여 파일을 읽을 수 있음
      const reader = new FileReader();
      // 파일을 성공적으로 읽었을 때 발생하는 이벤트
      reader.onload = (e) => {
        // 이미지 파일이 읽혔을 때 발생하는 onload 이벤트 핸들러에서
        // `e.target.result`를 사용하여 데이터 URL을 가져와 this.imageUrl 변수에 할당
        this.imageUrl = e.target.result;
      };
      // FileReader 객체를 사용하여 이미지 파일을 데이터 URL로 읽어들임
      // 데이터 URL은 파일을 텍스트 형식으로 인코딩한 문자열
      reader.readAsDataURL(this.image);
    },

    toggleEditCard(index) {
      this.showEditCardIndex = this.showEditCardIndex === index ? -1 : index;
      this.editedMenu =
        this.showEditCardIndex === -1 ? {} : { ...this.menus[index] };
      console.log("ShowEditCardIndex: ", this.showEditCardIndex);
      console.log("EditedMenu: ", this.editedMenu);
    },
    async saveChanges() {
      try {
        // 이미지가 존재하는 경우에만 이미지 URL 추가
        if (this.image) {
          await this.upload(); // 이미지 업로드
          this.editedMenu.fimg = this.imageUrl; // 업로드된 이미지 경로를 editedMenu에 저장
        }
        const res = await axios.post(`/B_MenuReUpdate`, this.editedMenu);
        console.log("Response from B_MenuReUpdate:", res.data);
        await this.menuList();
        this.showEditCardIndex = -1;

        alert("메뉴가 성공적으로 수정되었습니다.");

        console.log("Menu saved successfully.");
      } catch (error) {
        console.error("Error saving changes:", error);
      }
    },
    async confirmDelete(fnum) {
      if (confirm("정말 삭제하시겠습니까?")) {
        await this.deleteMenu(fnum);
      }
    },
    async deleteMenu(fnum) {
      try {
        const res = await axios.post(`/B_MenuDelete`, { fnum });
        console.log("Response from B_MenuDelete:", res.data);

        await this.menuList();
        console.log("Menu deleted successfully.");
        alert("삭제되었습니다."); // 삭제 성공 메시지
      } catch (error) {
        console.error("Error deleting menu:", error);
      }
    },
  },
};
</script>
